@import url("https://fonts.googleapis.com/css2?family=Antonio&display=swap");

html {
  box-sizing: border-box;
}

body {
  margin: 0;
  min-height: 100vh;
  font-family: "Antonio", sans-serif;
  color: #444;
  background: #f2f2f2;
}

h1 {
  text-transform: uppercase;
  text-align: center;
  margin: 25px 0;
  font-size: 40px;
  letter-spacing: 3px;
}

.loader {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #f2f2f2;
}

.loader img {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.container {
  display: flex;
  justify-content: center;
}

/* Image Container */
.image-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 1em;
  max-width: 1000px;
  margin: 10px 30px;
}

.image-container img {
  width: 100%;
  margin-top: 5px;
}

/* Media Query: Large Smartphone */
@media screen and (max-width: 600px) {
  h1 {
    font-size: 20px;
  }

  .image-container {
    margin-top: 10px;
  }
}
